var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"w-full"},[_vm._ssrNode("<img"+(_vm._ssrAttr("src",require("assets/images/SparkHero.png")))+" alt class=\"kb-img-max-h object-cover w-full\" data-v-48047678> <div class=\"p-4 flex flex-col items-center text-center mx-auto my-8\" data-v-48047678><h1 class=\"main-font text-9xl uppercase\" data-v-48047678>Spark App</h1> <p data-v-48047678>\n      A <strong data-v-48047678>collaborative storytelling app</strong> helping connect the\n      Asian diaspora within Canada.\n    </p> <div class=\"flex gap-4 py-4\" data-v-48047678><p class=\"tag\" data-v-48047678>Capstone</p> <p class=\"tag\" data-v-48047678>UX Research</p> <p class=\"tag\" data-v-48047678>UI Design</p></div></div> <div class=\"p-4 kb-max-w my-16\" data-v-48047678><h2 class=\"overline mb-8\" data-v-48047678>Project Info</h2> <div class=\"flex flex-col md:flex-row gap-16 md:gap-4\" data-v-48047678><div class=\"flex-1 flex gap-8 flex-wrap\" data-v-48047678><p class=\"w-5/12\" data-v-48047678><strong data-v-48047678>Project Type</strong><br data-v-48047678>\n          Independent Capsone\n        </p> <p class=\"w-5/12\" data-v-48047678><strong data-v-48047678>Team</strong><br data-v-48047678>\n          Solo\n        </p> <p class=\"w-5/12\" data-v-48047678><strong data-v-48047678>My Role</strong> <br data-v-48047678>\n          UX Designer <br data-v-48047678>UI Designer\n        </p> <p class=\"w-5/12\" data-v-48047678><strong data-v-48047678>Timeline</strong><br data-v-48047678>\n          6 months\n        </p> <p class=\"w-5/12\" data-v-48047678><strong data-v-48047678>Tools</strong><br data-v-48047678>\n          Figma <br data-v-48047678>Miro <br data-v-48047678>Condens.io <br data-v-48047678>Otter.io\n        </p></div> <div class=\"flex-1\" data-v-48047678>\n        Over a 6 month period between September 2020 to April 2021, I worked\n        on this project as my capstone in my final year of Sheridan’s\n        Interaction Design program. The inspiration came grew from\n        conversations with Asian-Canadian friends and genuine, deep-seated\n        curiosity.\n      </div></div></div> <div class=\"p-4 my-16 kb-max-w\" data-v-48047678><h2 class=\"overline mb-8\" data-v-48047678>Introduction</h2> <div class=\"flex flex-col md:flex-row gap-8\" data-v-48047678><h3 class=\"flex-1 heading\" data-v-48047678>Immigration is hard.</h3> <p class=\"flex-1\" data-v-48047678>\n        Around 300 000 people uproot and immigrate to Canada each year, with\n        the top three origins of foreign-born people being from India, China,\n        and the Philippines.\n        <br data-v-48047678><br data-v-48047678>\n        Immigrants experience multiple layers of stress, also known as\n        acculturation stress, which can significantly impact their mental and\n        emotional well-being. This can result in a loss of cultural norms,\n        social status, religious customs, social support systems, adjusting to\n        a new culture and experiencing changes in identity.\n      </p></div></div> <div class=\"flex flex-col md:flex-row gap-16 items-center my-16\" data-v-48047678><div class=\"flex-1\" data-v-48047678><img"+(_vm._ssrAttr("src",require("assets/images/SparkProblemStatement.png")))+" alt class=\"kb-img-max-h object-contain\" data-v-48047678></div> <div class=\"flex-1 p-4\" data-v-48047678><h2 class=\"overline mb-4\" data-v-48047678>Problem Statement</h2> <p class=\"subheading-2\" style=\"max-width: 620px\" data-v-48047678>\n        How might we help foster more positive cultural connections within 1st\n        and 2nd gen Asian-Canadians?\n      </p></div></div> <div class=\"my-16 p-4 kb-max-w\" data-v-48047678><h2 class=\"overline mb-8\" data-v-48047678>Research &amp; Findings</h2> <div class=\"flex flex-col md:flex-row gap-16\" data-v-48047678><div class=\"flex-1\" data-v-48047678><h3 class=\"heading\" data-v-48047678>\n          Speaking with 15 Asian-Canadians about their cultural transitions.\n        </h3></div> <div class=\"flex-1\" data-v-48047678><p data-v-48047678>\n          I conducted interviews with 15 1st and 2nd gen Asian-Canadians aged\n          18-31 with origins from the Philippines, China, India, Korea,\n          Vietnam, and Pakistan. Many factors came into play when building\n          connection, the most influential being their past experiences and\n          their family’s transition into Canadian culture.\n        </p></div></div></div> <div class=\"my-16\" data-v-48047678><img"+(_vm._ssrAttr("src",require("assets/images/SparkResearch1.png")))+" alt data-v-48047678></div> <div class=\"my-16 p-4 kb-max-w text-center\" data-v-48047678><div class=\"overline mb-4\" data-v-48047678>RESEARCH QUESTION</div> <div class=\"subheading-2 w-8/12 mx-auto\" data-v-48047678>\n      What challenges do Asian-Canadians face when connecting to their ethnic\n      culture?\n    </div></div> <div class=\"my-16 p-4 kb-max-w\" data-v-48047678><div class=\"flex justify-center items-center gap-4 my-8\" data-v-48047678><div class=\"flex-1\" data-v-48047678><img"+(_vm._ssrAttr("src",require("assets/images/SparkResearch2.png")))+" alt class=\"kb-img-max-h object-cover\" data-v-48047678></div> <div class=\"flex-1\" data-v-48047678><p class=\"my-4\" data-v-48047678>\n          01\n          <br data-v-48047678> <strong data-v-48047678>The influences of their two cultures frequently pull them in\n            opposite directions causing internal pressure and conflict about\n            their personal identity and social place</strong></p> <p class=\"my-4\" data-v-48047678>\n          02\n          <br data-v-48047678> <strong data-v-48047678>How well the participants spoke their parents' languages affected\n            their personal identity greatly.\n          </strong></p> <p class=\"my-4\" data-v-48047678>\n          03\n          <br data-v-48047678> <strong data-v-48047678>Those living in or who grew up in white-dominant populations felt\n            a greater pressure to conform and repress their ethnic\n            culture.</strong></p></div></div> <div class=\"flex justify-center items-center gap-4 my-8\" data-v-48047678><div class=\"flex-1\" data-v-48047678><p class=\"my-4\" data-v-48047678>\n          04\n          <br data-v-48047678> <strong data-v-48047678>Connection to culture primarily depends on family’s influence\n            which lessens over time.</strong></p> <p class=\"my-4\" data-v-48047678>\n          05\n          <br data-v-48047678> <strong data-v-48047678>Asian-Canadians desire to connect, but don’t know how or where to\n            start.\n          </strong></p></div> <div class=\"flex-1\" data-v-48047678><img"+(_vm._ssrAttr("src",require("assets/images/SparkResearch3.png")))+" alt class=\"w-full kb-img-max-h object-cover\" style=\"height: 400px\" data-v-48047678></div></div></div> <div class=\"my-16 p-4 kb-max-w\" data-v-48047678><div class=\"subheading mb-4 text-center\" data-v-48047678>Co-Creation Agenda</div> <div class=\"flex gap-8\" data-v-48047678><div class=\"flex-1\" data-v-48047678><img"+(_vm._ssrAttr("src",require("assets/images/LifeMapGraphic.png")))+" alt class=\"coImg\" data-v-48047678> <p data-v-48047678><strong data-v-48047678>1. Life Map</strong> <br data-v-48047678>\n          Participants first mapped out their cultural journey on a timeline,\n          adding significant life events, how they’ve connected or\n          disconnected to their ethnic culture, and emotions.\n          <br data-v-48047678><br data-v-48047678> <strong data-v-48047678>Why</strong> <br data-v-48047678>\n          It was a highly reflective activity that gave context to their goals\n          and motivations over time while also getting them in the mindset for\n          co-creating.\n        </p></div> <div class=\"flex-1\" data-v-48047678><img"+(_vm._ssrAttr("src",require("assets/images/EmpathyMapGraphic.png")))+" alt class=\"coImg\" data-v-48047678> <p data-v-48047678><strong data-v-48047678>2. Empathy Map</strong> <br data-v-48047678>\n          After thinking a lot about the past, this activity focused on their\n          present.\n          <br data-v-48047678><br data-v-48047678> <strong data-v-48047678>Why</strong> <br data-v-48047678>\n          It was to help me better understand what goes on in their day-to-day\n          and how their culture affects them.\n        </p></div> <div class=\"flex-1\" data-v-48047678><img"+(_vm._ssrAttr("src",require("assets/images/QuestionnaireGraphic.png")))+" alt class=\"coImg\" data-v-48047678> <p data-v-48047678><strong data-v-48047678>3. Questionnaire</strong> <br data-v-48047678>\n          Next, they were to answer 3 questions to find out how they wanted to\n          connect with their ethnic culture.\n          <br data-v-48047678><br data-v-48047678> <strong data-v-48047678>Why</strong> <br data-v-48047678>\n          This really clarified many assumptions I had surrounding the\n          solution.\n        </p></div> <div class=\"flex-1\" data-v-48047678><img"+(_vm._ssrAttr("src",require("assets/images/Crazy8sGraphic.png")))+" alt class=\"coImg\" data-v-48047678> <p data-v-48047678><strong data-v-48047678>4. Crazy 8’s</strong> <br data-v-48047678>\n          Lastly, using the challenge statement participants were to think\n          about all the activities they had just completed and try to come up\n          with as many solutions to the challenge as possible.\n          <br data-v-48047678><br data-v-48047678> <strong data-v-48047678>Why</strong> <br data-v-48047678>\n          This activity helped generate more ideas from the target audience\n          themselves, to help mitigate my personal biases towards solutions.\n        </p></div></div></div> <div class=\"flex flex-col md:flex-row gap-16 items-center my-16\" data-v-48047678><div class=\"flex-1\" data-v-48047678><img"+(_vm._ssrAttr("src",require("assets/images/SparkStorytellingInterview.png")))+" alt class=\"kb-img-max-h object-contain\" data-v-48047678></div> <div class=\"flex-1 p-4\" data-v-48047678><h2 class=\"subheading mb-4\" data-v-48047678>Storytelling Interview</h2> <p style=\"max-width: 620px\" data-v-48047678>\n        I conducted 5 semi-structured storytelling interviews with pairs of\n        siblings and cousins aged 17-25, with either individuals being a 1st\n        or 2nd gen Asian-Canadian. The participant ethnic origins from the\n        Philippines, China, Vietnam, Thailand, and Laos.\n        <br data-v-48047678><br data-v-48047678>\n        During the interviews, participants were asked to collaboratively tell\n        a story of a favourite shared memory. This was for the purpose of\n        observing how Asian-Canadian family members interact with each other\n        when telling a story and uncover the underlying structure of stories.\n      </p></div></div> <div class=\"w-full orange-bg py-16\" data-v-48047678><div class=\"kb-max-w text-4xl text-white text-center\" data-v-48047678><span class=\"opacity-50 text-8xl\" data-v-48047678>“</span><br data-v-48047678>\n      Memories are hard to recall on the spot. An experience worth telling,\n      must be funny or interesting.\n    </div></div> <div class=\"my-16 p-4 kb-max-w\" data-v-48047678><h2 class=\"overline mb-8\" data-v-48047678>Ideation</h2> <div class=\"flex flex-col md:flex-row gap-16\" data-v-48047678><div class=\"flex-1\" data-v-48047678><h3 class=\"heading\" data-v-48047678>\n          A better platform for sharing life experiences.\n        </h3></div> <div class=\"flex-1\" data-v-48047678><p class=\"mb-4\" data-v-48047678>\n          An app that allows friends and families to retell and recreate\n          experiences in a collaborative space, helping them preserve their\n          memories and traditions from anywhere in the world. It lets users\n          find their cultural community, share stories, and provides people a\n          place to learn about themselves and their culture.\n        </p> <p data-v-48047678><strong data-v-48047678>Pros</strong></p> <ul class=\"list-disc pl-4\" data-v-48047678><li data-v-48047678>\n            There currently isn’t a collaborative creation platform just for\n            Asian-Canadians\n          </li> <li data-v-48047678>Encourages all perspectives and opinions</li> <li data-v-48047678>\n            Allows ESL users to communicate easily with English speakers\n          </li></ul> <p class=\"mt-4\" data-v-48047678><strong data-v-48047678>Cons</strong></p> <ul class=\"list-disc pl-4\" data-v-48047678><li data-v-48047678>Frustration could arise with older less tech-savvy users</li> <li data-v-48047678>\n            Frustration could arise in the younger users having to teach their\n            older family members how to use the app\n          </li></ul></div></div></div>")])}
var staticRenderFns = []

